/* eslint no-await-in-loop: 0 */
import Immutable from 'immutable'
import { fetch, fetchErrModal, curTagChange } from 'base/utils.js'
import { Modal } from 'antd'

const RECEIVE_SUCCESS_ENDPOINTS = 'RECEIVE_SUCCESS_ENDPOINTS'
const RECEIVE_SUCCESS_COUNTERS = 'RECEIVE_SUCCESS_COUNTERS'
const RECEIVE_SUCCESS_CHARTSDATA = 'RECEIVE_SUCCESS_CHARTSDATA'

let totalResCount = 0
let responseCount = 0

/*
  isLoading分几个状态，分别对应不同的值：
  1.请求未开始：-1
  2.请求开始：0
  3.响应结束：1
*/
const initialState = Immutable.fromJS({
  endpoints: [],
  counters: [],
  searchText: { endpoint: '', tag: '', counter: '', selectedEndpointRows: [], selectedCounterRows: [] },
  selectedEndpoints: [],
  selectedCounters: [],
  title: '',
  start: '',
  end: '',
  cf: 'AVERAGE',
  method: 'nosum',
  chartsData: [],
  isLoading: -1,
})

export function resetDashboardState() {
  return { type: 'RESET_DASHBOARD_STATE' }
}

export function saveSearchData(data) {
  return {
    type: 'SAVE_SEARCH_DATA',
    payload: data,
  }
}

export function saveSearchText(data) {
  return {
    type: 'SAVE_SEARCH_TEXT',
    searchText: data,
  }
}

function receiveSuccessEndpoints(data) {
  return {
    type: RECEIVE_SUCCESS_ENDPOINTS,
    payload: {
      endpoints: data,
    },
  }
}

function receiveSuccessCounters(data) {
  return {
    type: RECEIVE_SUCCESS_COUNTERS,
    counters: data,
  }
}

function receiveSuccessChartsData(data) {
  return {
    type: RECEIVE_SUCCESS_CHARTSDATA,
    chartsData: data,
  }
}

export function getEndpoints(type, endpoint, tag, limit) {
  // 如果没有输入有效数值，就不传这个值了，比如endpoint输入无效值，就不给endpoint传参数了
  return (dispatch) => {
    return fetch(type, {params:{tag, query:endpoint, limit}})
      .then((rsp) => {
        dispatch(receiveSuccessEndpoints(rsp))
        if (rsp.length === 0) {
          Modal.warning({
            title: '提示',
            content: '响应数据是空！',
          })
          return false
        }
      })
      .catch(() => {
        Modal.warning({
          title: '提示',
          content: 'endpoints获取失败！',
        })
      })
  }
}

export function getCounters(ids, query, limit) {
  return (dispatch) => {
    // 请求开始时，重置isLoading的值
    dispatch({ type: 'START_REQUEST_COUNTERS' })
    return fetch('getCounters', {params:{query, limit, ids}})
      .then((rsp) => {
        dispatch(receiveSuccessCounters(rsp))
        if (rsp.length === 0) {
          Modal.warning({
            title: '提示',
            content: '响应数据是空！',
          })
          return false
        }
      })
      .catch(() => {
        dispatch({ type: 'RECEIVE_FAIL_COUNTERS' })
        Modal.warning({
          title: '提示',
          content: 'counters获取失败！',
        })
      })
  }
}

export function getCharts(consol_fun, endpoints, counters, title, startTime, endTime) {
  totalResCount = 0
  responseCount = 0
  return (dispatch) => {
    // 首次并发,清空接收画图数据的数组
    dispatch({ type: 'CLEAR_DASHBOARD_VIEWS' })
    // 请求开始时把isLoading置为0
    dispatch({ type: 'START_REQUEST_CHARTSDATA' })
    const results = [] // 用于存放所有响应
    // 判断是哪个视角，计算请求总数，开始并发请求
    if (title === 'Endpoint') {
      totalResCount = counters.length
      let length = totalResCount
      while (length) {
        results.push({ rsp: [] })
        length -= 1
      }
      const sortCounters = counters.sort()
      sortCounters.forEach((eachCounter, index) => {
        const params = {
          consol_fun,
          counters: [eachCounter],
          end_time: endTime,
          hostnames: endpoints,
          start_time: startTime,
        }
        results[index].params = params
        fetch('getCharts', {body: params})
        .then((rsp) => {
          results[index].rsp = rsp
          dispatch(receiveSuccessChartsData(results))
          responseCount += 1
          if (totalResCount === responseCount) {
            dispatch({ type: 'FINISHED_RESPONSE' })
          }
        })
        .catch(() => {
          // 响应失败的情况
          responseCount += 1
          if (totalResCount === responseCount) {
            dispatch({ type: 'FINISHED_RESPONSE' })
          }
        })
      })
    } else if (title === 'Counter') {
      totalResCount = endpoints.length
      let length = totalResCount
      while (length) {
        results.push({ rsp: [] })
        length -= 1
      }
      const sortEndpoints = endpoints.sort()
      sortEndpoints.forEach((eachEndpoint, index) => {
        const params = {
          consol_fun,
          counters,
          end_time: endTime,
          hostnames: [eachEndpoint],
          start_time: startTime,
        }
        results[index].params = params
        fetch('getCharts', {body: params})
        .then((rsp) => {
          results[index].rsp = rsp
          dispatch(receiveSuccessChartsData(results))
          responseCount += 1
          if (totalResCount === responseCount) {
            dispatch({ type: 'FINISHED_RESPONSE' })
          }
        })
        .catch(() => {
          // 响应失败的情况
          responseCount += 1
          if (totalResCount === responseCount) {
            dispatch({ type: 'FINISHED_RESPONSE' })
          }
        })
      })
    } else {
      // 组合视角
      totalResCount = 1
      const params = {
        consol_fun,
        counters,
        end_time: endTime,
        hostnames: endpoints,
        start_time: startTime,
      }
      const postParams = JSON.stringify(params)
      results.push({ rsp: [], params })
      fetch('getCharts', postParams)
      .then((rsp) => {
        results[0].rsp = rsp
        dispatch(receiveSuccessChartsData(results))
        responseCount += 1
        if (totalResCount === responseCount) {
          dispatch({ type: 'FINISHED_RESPONSE' })
        }
      })
      .catch(() => {
        // 响应失败的情况
        responseCount += 1
        if (totalResCount === responseCount) {
          dispatch({ type: 'FINISHED_RESPONSE' })
        }
      })
    }
  }
}
// 获得id是用于在URL上设置的
export function getId(endpoints, counters, title, startTime, endTime, singleCounter) {
  return (dispatch) => {
    const params = JSON.stringify({ endpoints, counters })
    const win = window.open('about:blank', '_blank')
    fetch('tmpGraphAdd', {body:{endpoints, counters}}).then((rsp) => {
      const data = {
        id: rsp.id,
        endpoints,
        counters,
        title,
        startTime,
        endTime,
        cf: 'AVERAGE',
        method: 'nosum',
      }
      dispatch(saveSearchData(data))
      let graph_type = 'h'
      if (title === 'Counter') {
        graph_type = 'k'
      }
      if (title === '组合') {
        graph_type = 'a'
      }
      // 得到id后打开新页面
      // const protocol = window.location.protocol
      // const host = window.location.host
      if (singleCounter === undefined) {
        const query = `id=${rsp.id}&graph_type=${graph_type}&cf=AVERAGE&start=${startTime}&end=${endTime}`
        win.location.href = `/dashboard/charts?${query}`
        // window.open(`${protocol}//${host}/#/dashboard/charts?${query}`, '_blank')
        return false
      }
      const query = `id=${rsp.id}&graph_type=h&cf=AVERAGE&start=-3600`
      win.location.href = `/chart?${query}`
      // window.open(`${protocol}//${host}/#/chart?${query}`, '_blank')
    })
    .catch(() => {
      Modal.warning({
        title: '提示',
        content: '响应失败！',
      })
    })
  }
}
// 获得endpoints和counters去请求画图参数
export function getChartParams(id, graph_type, startTime, endTime, consol_fun, method) {
  return (dispatch) => {
    fetch('tmpGraphGet', {path:`/${id}`}).then((rsp) => {
      // 修改method, id, searchData的值
      const { endpoints, counters } = rsp
      let title = 'Endpoint'
      if (graph_type === 'k') {
        title = 'Counter'
      }
      if (graph_type === 'a') {
        title = '组合'
      }
      const data = {
        id,
        endpoints,
        counters,
        title,
        startTime,
        endTime,
        cf: consol_fun,
        method,
      }
      dispatch(saveSearchData(data))
      // 接下来用这个参数去请求画图数据
      dispatch(getCharts(consol_fun, endpoints, counters, title, startTime, endTime))
    })
    .catch(() => {
      Modal.warning({
        title: '提示',
        content: '响应失败！',
      })
    })
  }
}
const midReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'RECEIVE_SUCCESS_ENDPOINTS':
      return state.set('endpoints', action.payload.endpoints)
    case 'START_REQUEST_COUNTERS':
      return state.set('isLoading', 0)
    case 'START_REQUEST_CHARTSDATA':
      return state.set('isLoading', 0)
    case 'RECEIVE_SUCCESS_COUNTERS':
      return state.merge({
        isLoading: 1,
        counters: action.counters,
      })
    case 'RECEIVE_FAIL_COUNTERS':
      return state.set('isLoading', 1)
    case 'SAVE_SEARCH_TEXT':
      return state.set('searchText', action.searchText)
    case 'SAVE_SEARCH_DATA':
      return state.merge({
        id: action.payload.id,
        selectedEndpoints: action.payload.endpoints,
        selectedCounters: action.payload.counters,
        title: action.payload.title,
        start: action.payload.startTime,
        end: action.payload.endTime,
        cf: action.payload.cf,
        method: action.payload.method,
      })
    case 'RECEIVE_SUCCESS_CHART_PARAMS':
      return state.set('searchData', action.payload)
    case 'CLEAR_DASHBOARD_VIEWS':
      return state.set('chartsData', [])
    case 'RECEIVE_SUCCESS_CHARTSDATA':
      return state.set('chartsData', action.chartsData)
    case 'FINISHED_RESPONSE':
      return state.set('isLoading', 1)
    default:
      return state
  }
}

export default function dashboard(state, action) {
  if (action.type === 'RESET_DASHBOARD_STATE') {
    state = undefined
  }
  return midReducer(state, action)
}






